
@component-namespace page {
@component loadmore {
@descendent desc {
      text-align: center;
      color: #666;
      padding-bottom: 5px;
&:last-of-type {
        border-bottom: solid 1px #eee;
}
}
@descendent listitem {
      height: 50px;
      line-height: 50px;
      border-bottom: solid 1px #eee;
      text-align: center;
&:first-child {
        border-top: solid 1px #eee;
}
}
@descendent wrapper {
      overflow: scroll;
}
.mint-spinner {
      display: inline-block;
      vertical-align: middle;
}
}
}
@component mint-loadmore-bottom {
span {
    display: inline-block;
    transition: .2s linear;
    vertical-align: middle;
@when rotate {
      -webkit-transform: rotate(180deg);
              transform: rotate(180deg);
}
}
}



