
@component-namespace page {
@component msgbox {
@descendent wrapper {
      padding: 0 20px;
      position: absolute 50% * * *;
      width: 100%;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%);
button:not(:last-child) {
        margin-bottom: 20px;
}
}
}
}



