
@import "../../../src/style/var.css";
@component-namespace mint {
@component search {
    height: 100%;
    height: 100vh;
    overflow: hidden;
}
@component searchbar {
    position: relative;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    background-color: $color-grey;
    box-sizing: border-box;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding: 8px 10px;
    z-index: 1;
@descendent inner {
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      background-color: $color-white;
      border-radius: 2px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      height: 28px;
      padding: 4px 6px;
.mintui-search {
        font-size: 12px;
        color: $color-grey;
}
}
@descendent core {
      -webkit-appearance: none;
         -moz-appearance: none;
              appearance: none;
      border: 0;
      box-sizing: border-box;
      width: 100%;
      height: 100%;
      outline: 0;
}
@descendent cancel {
      color: $color-blue;
      margin-left: 10px;
      text-decoration: none;
}
}
@component search-list {
    overflow: auto;
    padding-top: 44px;
    position: absolute 0 0 0 0;
}
}



