
@component-namespace page {
@component range {
.mt-range {
      width: 100%;
}
.mint-cell-value {
      -webkit-box-flex: 2.5;
          -ms-flex: 2.5;
              flex: 2.5;
      position: relative;
}
@descendent header {
      margin-bottom: 8px;
      padding-left: 15px;
&:not(:first-of-type) {
        margin-top: 28px;
}
}
@descendent desc {
      padding: 5px 0 5px 5%;
}
}
}



