
@component-namespace page {
@component progress {
.mint-cell-value {
      -webkit-box-flex: 2.5;
          -ms-flex: 2.5;
              flex: 2.5;
      position: relative;
      top: -20px;
}
.mt-progress {
      width: 100%;
      position: absolute;
      top: 5px;
}
@descendent wrapper {
      padding: 0 10px;
      margin-top: 50px;
.mt-progress {
        position: relative;
}
.progress-fade-transition {
        transition: opacity .3s;
}
.progress-fade-enter,
      .progress-fade-leave {
        opacity: 0;
}
}
}
}



