
@import "../../../src/style/var.css";
@component-namespace mint {
@component navbar {
    background-color: $color-white;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    text-align: center;
@when fixed {
      position: fixed 0 0 * 0;
      z-index: $z-index-normal;
}
.mint-tab-item {
      padding: 17px 0;
      font-size: 15px;
&:last-child {
        border-right: 0;
}
&.is-selected {
        border-bottom: 3px solid $color-blue;
        color: $color-blue;
        margin-bottom: -3px;
}
}
}
}



