
@component-namespace mint-spinner {
@component fading-circle {
    position: relative;
@descendent circle {
      size: 100%;
      position: absolute 0 * * 0;
&::before {
        content: " ";
        display: block;
        margin: 0 auto;
        size: 15%;
        border-radius: 100%;
        -webkit-animation: mint-fading-circle 1.2s infinite ease-in-out both;
                animation: mint-fading-circle 1.2s infinite ease-in-out both;
}
@for $i from 2 to 12 {
&.is-circle$i {
          -webkit-transform: rotate(calc(360deg / 12 * ($i - 1)));
                  transform: rotate(calc(360deg / 12 * ($i - 1)));
&::before {
            -webkit-animation-delay: calc(-1.2s + 1.2s / 12 * ($i - 1));
                    animation-delay: calc(-1.2s + 1.2s / 12 * ($i - 1));
}
}
}
}
}
@-webkit-keyframes mint-fading-circle {
0%, 39%, 100% { opacity: 0
}
40% { opacity: 1
}
}
@keyframes mint-fading-circle {
0%, 39%, 100% { opacity: 0
}
40% { opacity: 1
}
}
}



