
@import "../../../src/style/var.css";
@component-namespace mint {
@component header {
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    background-color: $color-blue;
    box-sizing: border-box;
    color: $color-white;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    font-size: 14px;
    height: $header-height;
    line-height: 1;
    padding: 0 10px;
    position: relative;
    text-align: center;
    white-space: nowrap;
.mint-button {
      background-color: transparent;
      border: 0;
      box-shadow: none;
      color: inherit;
      display: inline-block;
      padding: 0;
      font-size: inherit;
&::after {
        content: none;
}
}
@descendent button {
      -webkit-box-flex: .5;
          -ms-flex: .5;
              flex: .5;
> a {
        color: inherit;
}
@when left {
        text-align: left;
}
@when right {
        text-align: right;
}
}
@descendent title {
@utils-ellipsis;
      font-size: inherit;
      font-weight: normal;
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
}
@when fixed {
      position: fixed 0 0 * 0;
      z-index: $z-index-normal;
}
}
}



