
@component-namespace mint-spinner {
@component triple-bounce {
@descendent bounce1, bounce2, bounce3 {
      border-radius: 100%;
      display: inline-block;
      -webkit-animation: mint-spinner-triple-bounce 1.4s infinite ease-in-out both;
              animation: mint-spinner-triple-bounce 1.4s infinite ease-in-out both;
}
@descendent bounce1 {
      -webkit-animation-delay: -0.32s;
              animation-delay: -0.32s;
}
@descendent bounce2 {
      -webkit-animation-delay: -0.16s;
              animation-delay: -0.16s;
}
}
}
@-webkit-keyframes mint-spinner-triple-bounce {
0%, 80%, 100% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
40% {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
}
}
@keyframes mint-spinner-triple-bounce {
0%, 80%, 100% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
40% {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
}
}



