
@import "../../../src/style/var.css";
@component-namespace mint {
@component tab-item {
    display: block;
    padding: 7px 0;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    text-decoration: none;
@descendent icon {
      size: 24px;
      margin: 0 auto 5px;
&:empty {
        display: none;
}
& > * {
        display: block;
        size: 100%;
}
}
@descendent label {
      color: inherit;
      font-size: $tab-item-font-size;
      line-height: 1;
}
}
}



