
@component-namespace mint {
@component msgbox {
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate3d(-50%, -50%, 0);
            transform: translate3d(-50%, -50%, 0);
    background-color: #fff;
    width: 85%;
    border-radius: 3px;
    font-size: 16px;
    -webkit-user-select: none;
    overflow: hidden;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    transition: .2s;
@descendent header {
      padding: 15px 0 0;
}
@descendent content {
      padding: 10px 20px 15px;
      border-bottom: 1px solid #ddd;
      min-height: 36px;
      position: relative;
}
@descendent input {
      padding-top: 15px;
& input {
        border: 1px solid #dedede;
        border-radius: 5px;
        padding: 4px 5px;
        width: 100%;
        -webkit-appearance: none;
           -moz-appearance: none;
                appearance: none;
        outline: none;
}
& input.invalid {
        border-color: #ff4949;
&:focus {
          border-color: #ff4949;
}
}
}
@descendent errormsg {
      color: red;
      font-size: 12px;
      min-height: 18px;
      margin-top: 2px;
}
@descendent title {
      text-align: center;
      padding-left: 0;
      margin-bottom: 0;
      font-size: 16px;
      font-weight: bold;
      color: #333;
}
@descendent message {
      color: #999;
      margin: 0;
      text-align: center;
      line-height: 36px;
}
@descendent btns {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      height: 40px;
      line-height: 40px;
}
@descendent btn {
      line-height: 35px;
      display: block;
      background-color: #fff;
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      margin: 0;
      border: 0;
&:focus {
        outline: none;
}
&:active {
        background-color: #fff;
}
}
@descendent cancel {
      width: 50%;
      border-right: 1px solid #ddd;
&:active {
        color: #000;
}
}
@descendent confirm {
      color: #26a2ff;
      width: 50%;
&:active {
       color: #26a2ff;
}
}
}
}
.msgbox-bounce-enter {
  opacity: 0;
  -webkit-transform: translate3d(-50%, -50%, 0) scale(0.7);
          transform: translate3d(-50%, -50%, 0) scale(0.7);
}
.msgbox-bounce-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(-50%, -50%, 0) scale(0.9);
          transform: translate3d(-50%, -50%, 0) scale(0.9);
}



