
@import "../../../src/style/var.css";
@component-namespace mint {
@component badge {
    color: $color-white;
    text-align: center;
    display: inline-block;
@when success {
      background-color: $success-color;
}
@when primary {
      background-color: $color-blue;
}
@when error {
      background-color: $error-color;
}
@when warning {
      background-color: $warning-color;
}
@when size-normal {
      border-radius: 12px;
      font-size: 15px;
      padding: 2px 8px;
}
@when size-small {
      border-radius: 8px;
      font-size: 12px;
      padding: 2px 6px;
}
@when size-large {
      border-radius: 14px;
      font-size: 18px;
      padding: 2px 10px;
}
}
}



