
@component-namespace mt {
@component range {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 30px;
    line-height: 30px;
& > * {
      display: -ms-flexbox;
      display: flex;
      display: -webkit-box;
}
& *[slot=start] {
      margin-right: 5px;
}
& *[slot=end] {
      margin-left: 5px;
}
@descendent content {
      position: relative;
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      margin-right: 30px;
}
@descendent runway {
      position: absolute;
      top: 50%;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%);
      left: 0;
      right: -30px;
      border-top-color: #a9acb1;
      border-top-style: solid;
}
@descendent thumb {
      background-color: #fff;
      position: absolute;
      left: 0;
      top: 0;
      width: 30px;
      height: 30px;
      border-radius: 100%;
      cursor: move;
      box-shadow: 0 1px 3px rgba(0,0,0,.4);
}
@descendent progress {
      position: absolute;
      display: block;
      background-color: #26a2ff;
      top: 50%;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%);
      width: 0;
}
@modifier disabled {
      opacity: 0.5;
}
}
}



